'use strict';

import React from 'react';

let {func, object} = React.PropTypes;

let Demo = React.createClass({
	displayName: 'Demo',
	contextTypes: {
    flux: object.isRequired,
    router: func.isRequired
  },
  statics: {
    willTransitionTo(transition) {
      // if (!LoginStore.isLoggedIn()) {
      //   transition.redirect('/login', {}, {'nextPath' : transition.path});
      // }
      // transition.redirect('login', {}, {'nextPath' : transition.path});
    }
  },
  handlerHead() {
    this.props.headParams.setTitle("Demo | Cng ng golang Vit Nam");
    this.props.headParams.setDescription("demo set Description");
  },
  componentWillMount: function() {
    this.handlerHead();
  },  
	render() {
		return (
			<div>
				<h3>Demo</h3>
			</div>
		);
	}
});

export default Demo;
