/* @jsx React.DOM */
"use strict";

import React from "react";
import {Redirect, State} from "react-router";
import FluxComponent from 'flummox/component';
import authMixin from '../lib/authMixin';


let Login = React.createClass({
	displayName: 'Login',
	mixins: [State],
	contextTypes: {
    flux: React.PropTypes.object.isRequired,
    router: React.PropTypes.func.isRequired
  },
  componentWillMount () {
  	this.props.headParams.setTitle("Login | Cng ng golang Vit Nam");
  },
  componentDidMount () {
  },
  componentDidUpdate() {
  	if (this.props.logInState === 'success') {
  		var { router } = this.context;
  		var nextPath = router.getCurrentQuery().nextPath;
  		if (nextPath) {
        router.replaceWith(nextPath);
      } else {
	     	router.replaceWith('home');
      }
    }
  },
	render() {
		return (
			<div className="container">
				<div className="col s12 m8 offset-m2 l6 offset-l3">
					<div className="row">
						<div className="col s6 offset-s3 m6 offset-m3">
	            <div className="card-panel">
			          <div className="input-field">
			            <input ref="username" type="text" length="10" autoFocus={true} tabIndex={1} className="validate" />
			            <label className="active" for="username">username</label>
		            </div>
		            <div className="input-field">
			            <input ref="password" type="password" length="10" className="validate" tabIndex={2} />
			            <label className="active" for="password">password</label>
		            </div>
		            <div className="input-field">
									<span onClick={this.handleLogIn} tabIndex={3} className="waves-effect waves-red white btn z-depth-1 grey-text text-darken-2">
										Login
									</span>
		            </div>
	            </div>
	          </div>
	        </div>
				</div>
			</div>
		)
	},
	handleLogIn(e) {
		var username = this.refs.username.getDOMNode().value;
		var password = this.refs.password.getDOMNode().value;
		if(username != "" && password != "") {
			this.props.flux.getActions('authActions').LoginActions({
				username: username,
				password: password
			});
		}
	}
});

class LogIn extends React.Component {
  render() {
    return (
      <FluxComponent flux={this.props.flux} connectToStores={['authStore']}>
        <Login headParams={this.props.headParams}	 />
      </FluxComponent>
    );
  }
}

export default LogIn;




















