import { Flummox, Actions, Store } from 'flummox';

class AppStore extends Store { 
	constructor(flux) {
    super();

    let appActions = flux.getActions('appActions');
    this.register(appActions.TestActions, this.handleTest);

    this.state = {};
  }
  
  handleTest(val) {
  	this.state = val;
    this.emit('change');
  }

  getTest() {
  	return this.state;
  }

  test() {
    localStorage.token = 'test';
  }
}

export default AppStore;


