import { Flummox, Actions, Store } from 'flummox';

class AppStore extends Store {
	constructor(flux) {
    super();

    let authActions = flux.getActions('authActions');

    this.register(authActions.LoginActions, this.handleLogin);
    this.register(authActions.LoginStart, this.handleStart);
    this.register(authActions.loadUser, this.handleload);

    this.state = {test: 'hihi'};
    /*{
      currentUser: null,
      loadUserState: localStorage.user,
      loadUserError: null,
      logInState: null,
      logInError: null,
      logOutState: null,
      logOutError: null
    };*/
  }

  handleStart() {
    this.state.logInState = 'loading';
    this.emit('change');
  }

  handleload(val) {
    console.log('val', val);
  }

	handleLogin(req) {

    if(req.token) {
      // this.state.logInState = 'success';
      // this.state.currentUser = req.auth;
      this.setState({
        logInState: 'success'
      });
      // this.emit('change');
      localStorage.auth = req.auth;
      localStorage.token = req.token;
    } else {
      this.state.logInState = 'fail';
      this.state.logInError = req.fail;
  		this.emit('change');
    }
    // this.emit('Login');
	}

  getUser() {
    return {
      user: localStorage.user
    }
  }

  getState() {
  	return this.state;
  }

  test() {
    localStorage.token = 'test';
  }
}

export default AppStore;



