/* @jsx React.DOM */
'use strict';

import React from 'react';
import {RouteHandler} from 'react-router';
import FluxComponent from 'flummox/component';

let Filter = React.createClass({
	statics: {
		routerWillRunOnServer(state, flux, req) {
			console.log('req');
			return {};
		}
	},
	render() {
			console.log('FilterAuth');
		return this.props.children;
	}
});

let FilterAuth = React.createClass({
	displayName: 'FilterAuth',
  render() {
    return (
      <FluxComponent flux={this.props.flux} connectToStores={['authStore']}>
        <Filter>
          {this.props.children}
        </Filter>
      </FluxComponent>
    );
  }
});

class SignedInWrapper extends React.Component {
  render() {
    return (
      <FilterAuth flux={this.props.flux} >
        <RouteHandler {...this.props} />
      </FilterAuth>
    );
  }
}

export default SignedInWrapper;


